.gender-box{
    display: flex;
    flex-direction: column;
    border: 1px solid #C1C1C1;
    box-sizing: border-box;
    border-radius: 16px;
    width: 156px;
    height: 100px;
    margin-left: 16px;

    &:first-child{
        margin-left: 0;
    }

    &:hover{
        cursor: pointer;
    }

    
    
}

.gender-icon{
    width: 32px;
    height: 32px;
    align-self: center;
    margin-top: 20px;
}

.gender-text{
font-style: normal;
font-weight: normal;
font-size: 18px;
line-height: 22px;
color: #5B5B5B;
text-align: center;
margin-top: 8px;
}

.clicked{
    transform: scale(0.75);
    color: red;
}

.gender-choosen{
    color: #3C9C41;
    border-color: #3C9C41;
    text-decoration-color: #3C9C41;
}

@media only screen and (max-width: 767px) {

    .mobile-element{
        display: block;
    }
           
    .desktop-element{
        display: none;
    }

    .gender-box{
    width: 90px;
    height: 80px;
    }

   .gender-icon{
    margin: 10px 0 8px 0;
    }
    .secondary-title{
    font-size: 16px;
    line-height: 20px;
    color: #5B5B5B;
    }
    .gender-text{
    font-size: 16px;
    line-height: 20px;
    margin-top: 0;
    }
}